<template>
    <div>
        <el-card shadow="never" style="min-height: 600px;margin-bottom: 20px;">
            <el-collapse v-model="active" accordion>
                <el-collapse-item title="" name="deploy">
                    <div style="padding: 10px;line-height: 30px;color: #303133;font-size: 16px">
                        
                        <tt>index.html</tt> 
                        <tt>./static/configuration.json</tt> , 
                        <tt>github-api</tt> 
                        <tt>.github.io</tt> 
                        <tt>/static/configuration.json</tt>, 
                        <tt>npm run dev</tt> , , 
                        <tt>github</tt> </div>
                </el-collapse-item>
            </el-collapse>
        </el-card>
    </div>
</template>
<script>

    export default {
        data() {
            return {
                active: "deploy",
                deploy: {
                    status: "process",
                    active: 0,
                },
                use: {
                    status: "process",
                    active: 0,
                }
            }
        },
        mounted() {

        },
        methods: {
            deployChange(index) {
                this.deploy.status = index == 2 ? "success" : "process"
                this.deploy.active = index
            },
            useChange(index) {
                this.use.status = index == 2 ? "success" : "process"
                this.use.active = index
            }
        }
    }
</script>


